import React,{ Component }  from 'react';
import '../../App.css';
import { Card,Button, Popover } from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import { Link } from 'react-router-dom';

class mainCardShentai extends Component {
    state = {
        MDEX:'https://mdex.lg-qkl.xyz/'
    };

    // async componentDidMount(){
    //     //设置MDEX链接
    //     const url = await window.location.host;
    // }
 
  render(){
    const content = (
        <div>
          <p>将非链改网DAPP整合在链改网，使用时更快，更爽。无惧关网、打不开等问题</p>
          <p>需注意的是部分DAPP含有中心化内容，故而DAPP内中心化部分可能会存在异常，去中心化部分可放心使用</p>
        </div>
      );
    // const content = (
    //     <div>
    //         <Popover content={content} trigger="click">
    //             <QuestionCircleOutlined />  
    //         </Popover>
    //     </div>
    // );
    
  return (
            <Card type="inner" title='其它DAPP' extra={
                <Popover content={content} trigger="click">
                    <QuestionCircleOutlined />  
                </Popover>}
                style={{borderRadius:'10px'}}
                headStyle={{borderTopLeftRadius:'10px',borderTopRightRadius:'10px'}}
            >
                <div>
                    {/* <div >
                        <a href={this.state.MDEX}>
                            <div style={{width:'25%',float:'left'}}>
                                    <div style={{textAlign:'center'}}>
                                        <div className='MDEX' ></div>
                                    </div>
                                    <div style={{textAlign:'center',color:'black'}}>MDEX<br/></div>
                                </div>
                        </a>
                        <div style={{clear:'both'}}></div>
                    </div> */}

                    <div style={{marginTop:'15px'}}>
                        
                        
                        <div style={{clear:'both'}}></div>
                    </div>
                </div>
                <Link to="/edu">
                    <Button type='primary' danger block>知识库</Button>
                </Link>
                <Link to="/KnowledgeBase">
                    <Button block  style={{marginTop:'10px'}}>常用链接</Button>
                </Link>
                
              </Card>
  );
}
}

export default mainCardShentai;
